
.mainContainer{
	text-align: center;
	width: 100%;
	padding: 50px 0;	
}
.mainContent{
	display: block;
	margin: 0 auto;
	max-width: 1000px;
}
.shake{
	-webkit-animation: shake 0.5s;
	        animation: shake 0.5s;
}
@-webkit-keyframes shake {
    0% { -webkit-transform: translate(4px); transform: translate(4px); }
    10% { -webkit-transform: translate(-10px); transform: translate(-10px); }
    20% { -webkit-transform: translate(-7px); transform: translate(-7px); }
    30% { -webkit-transform: translate(8px); transform: translate(8px); }
    40% { -webkit-transform: translate(-6px); transform: translate(-6px); }
    50% { -webkit-transform: translate(7px); transform: translate(7px); }
    60% { -webkit-transform: translate(-5px); transform: translate(-5px); }
    70% { -webkit-transform: translate(3px); transform: translate(3px); }
    80% { -webkit-transform: translate(-1px); transform: translate(-1px); }
    90% { -webkit-transform: translate(1px); transform: translate(1px); }
    100% { -webkit-transform: translate(-2px); transform: translate(-2px); }
}
@keyframes shake {
    0% { -webkit-transform: translate(4px); transform: translate(4px); }
    10% { -webkit-transform: translate(-10px); transform: translate(-10px); }
    20% { -webkit-transform: translate(-7px); transform: translate(-7px); }
    30% { -webkit-transform: translate(8px); transform: translate(8px); }
    40% { -webkit-transform: translate(-6px); transform: translate(-6px); }
    50% { -webkit-transform: translate(7px); transform: translate(7px); }
    60% { -webkit-transform: translate(-5px); transform: translate(-5px); }
    70% { -webkit-transform: translate(3px); transform: translate(3px); }
    80% { -webkit-transform: translate(-1px); transform: translate(-1px); }
    90% { -webkit-transform: translate(1px); transform: translate(1px); }
    100% { -webkit-transform: translate(-2px); transform: translate(-2px); }
}


